'use client';

import { SITE_URL_LOCAL } from '@/app/config/app-config';
import styles from './comments.module.css';
import dynamic from 'next/dynamic';

const ReactCusdis = dynamic(() => import('cusdis'), {
  ssr: false,
  loading: () => <p>Carregando coment√°rios...</p>,
});

export type CommentsProps = {
  slug: string;
  title: string;
};

export const Comments = ({ slug, title }: CommentsProps) => {
  return (
    <div className={styles.component}>
      <ReactCusdis
        attrs={{
          host: 'https://cusdis.com',
          appId: 'd5aaf2c6-7e3d-40c7-9515-7c6d89018a89',
          pageId: slug,
          pageTitle: title,
          pageUrl: `${SITE_URL_LOCAL}/post/${slug}`,
          theme: 'auto',
        }}
        lang="pt-br"
      />
    </div>
  );
};
